<template>
  <div id="login">
    <div class="top">
      <img src="../../assets/images/login/logo.png" alt="" class="logo">
      <div class="title">广州博睿创新科技有限公司</div>
    </div>
    <div class="loginBox">
      <p class="title">网站后台管理系统</p>
      <el-form :model="loginForm" class="form">
        <el-form-item>
          <el-input v-model="loginForm.username" placeholder="账号"></el-input>
        </el-form-item>
        <el-form-item>
          <el-input v-model="loginForm.password" placeholder="密码" show-password></el-input>
        </el-form-item>
      </el-form>
      <div class="loginBtn" @click="login()">登录</div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "Login",
    data() {
      return {
        loginForm: {
          username: '',
          password: ''
        }
      }
    },
    methods: {
      //设置cookie
      createCookie(name, value, day) {
        /*var date = new Date();
        var expires = '';
        date.setTime(date.getTime() + (day*24*60*60*1000));
        expires = date.toGMTString();
        console.log(expires);
        document.cookie = name + '=' + value + ';expires=' + expires;
        console.log(document.cookie);*/
        var date = new Date();
        date.setDate(date.getDate() + day);
        document.cookie = name + '=' + value + ';expires='+ date;
      },
      //获取cookie
      getCookie(name) {
        var reg = RegExp(name + '=([^;]+)');
        var arr = document.cookie.match(reg);
        if (arr) {
          return arr[1];
        } else {
          return '';
        }
      },
      //删除cookie
      delCookie(name) {
        this.createCookie(name, null, -1);
      },
      login() {
        this.$axios.post('/user/manager', {
          username: this.loginForm.username,
          password: this.loginForm.password
        }).then((res) => {
          if (res.status === 200) {
            let data = res.data;
            localStorage.setItem("token", data.token);
            localStorage.setItem("expired", data.expired);
            this.createCookie('username',this.loginForm.username,7);
            this.createCookie('password',this.loginForm.password,7);
            this.$router.push('/index/statistics');
            this.$message.success('登录成功');
          } else {
            this.$message.error(res.msg);
          }
        }).catch((err) => {
          console.log(err);
        })
      }
    },
    mounted() {
      document.querySelector('body').setAttribute('style', 'background-color:#2F3031');
    },
    beforeDestroy() {
      document.querySelector('body').removeAttribute('style');
    },
  }
</script>

<style lang="less" scoped>
  @import "../../assets/style/login/login";
</style>
